import React, { useState }from "react";
import phoneService from "../Services/phone.js";
import Phone from "./Phone";

const Person = () => {
  const [persons, setPersons] = useState([]);
  const [errorMessage, setErrorMessage] = useState("some error happened...");
  const [error, toggleError] = useState(false);

  const handleDelete = id => {
    if (window.confirm("Are you sure you want to delete this person?")) {
      const url = `http://localhost:3001/persons/${id}`;
      const contact = persons.find(person => person.id === id);
      const contactToDelete = { ...contact };

      phoneService
        .noteDelete(id, contactToDelete)
        .then(returnedPerson => {
          setPersons(
            persons.filter(person => person.id !== contactToDelete.id)
          );
          setErrorMessage(
            `Note ${contactToDelete.name} was already removed from the phoneBook`
          );
          setTimeout(() => {
            setErrorMessage(null);
          }, 5000);
        })
        .catch(error => {
          alert(`This contact with ${id} was already deleted`);
        });
    }
  };

  const person_rows = () =>
    persons.map(person => {
      return (
        <Phone
          key={person.id}
          name={person.name}
          number={person.number}
          handleDelete={() => handleDelete(person.id)}
        />
      );
    });

  return <div>{person_rows()}</div>;
};

export default Person;
