import axios from 'axios';
const baseUrl = '/api/persons'

let token = null

const setToken = newToken => {
    token = `bearer ${newToken}`
  }

const getAll = () => {
    const request = axios.get(baseUrl)
    return request.then(response => response.data)
}

const create = async phoneObject => {
    const config = {
        headers: { Authorization: token },
      }

    const request = await axios.post(baseUrl, phoneObject, config)
    return request.then(response => response.data)
}

const noteDelete = (id, phoneObject) => {
    const request = axios.delete(`${baseUrl}/${id}`, phoneObject)
    return request.then(response => response.data)
}

const update = (id, phoneObject) => {
    const request = axios.put(`${baseUrl}/${id}`, phoneObject)
    return request.then(response => response.data)
}

export default { getAll, create, update, noteDelete, setToken }