import React, { useState, useEffect } from "react";
import Phone from "./Phone";
import phoneService from "../Services/phone.js";
import Notification from "./Error";
import Form from "./PhoneBook_Form";
import Person from "./PhoneBook_person";
import Loginform from "../Components/loginForm";
import loginService from "../Services/phone.js"
import Footer from "./Footer"

const Phonebook = () => {
  const [persons, setPersons] = useState([]);
  const [errorMessage, setErrorMessage] = useState("some error happened...");
  const [error, toggleError] = useState(false);
  const [user, setUser] = useState(null);

  useEffect(() => {
    phoneService.getAll().then(person => {
      setPersons(person);
    });
    toggleError(false);
  }, []);

  useEffect(() => {
    const loggedUserJson = window.localStorage.getItem('loggedPhonebookUser')
    if(loggedUserJson){
      const user = JSON.parse(loggedUserJson)
      setUser(user);
      loginService.setToken(user.token)
    }
  }, []);

  const handleLogout = () => {
    setUser(false)
    window.localStorage.removeItem('loggedPhonebookUser')
  }

  return (
    <div>
      <h2>Phonebook</h2>
      {error ? <Notification message={errorMessage} /> : null}
      {user === null ? (
        <Loginform />
      ) : (
        <div>
          <p>{user.name} logged in</p>
          <button onClick={handleLogout}>logout</button>
          <Form />
        </div>
      )}
      <h2>People</h2>
      <Person persons={persons} setPerson={setPersons}/>
      <Footer />
    </div>
  );
};

export default Phonebook;
