import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUserPlus } from "@fortawesome/free-solid-svg-icons";
import phoneService from "../Services/phone.js";

const Form = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("enter a name");
  const [newNumber, setNewNumber] = useState("XXX-XXX-XXXX");
  const [errorMessage, setErrorMessage] = useState("some error happened...");
  const [error, toggleError] = useState(false);

  const handleSubmit = event => {
    event.preventDefault();
    const newPhone = {
      name: newName,
      id: persons.length + 1,
      number: newNumber
    };

    const handleAlert = () => {
      toggleError(true);
      alert(`${newName} is already added to phonebook`);
    };

    const personExists = persons.map(person =>
      person.name === newPhone.name
        ? handleAlert()
        : setPersons(persons.concat(newPhone))
    );

    phoneService
      .create(newPhone)
      .then(returnedPerson => {
        setPersons(persons.concat(returnedPerson));
      })
      .catch(error => {
        setErrorMessage(error);
      });

    setNewName("");
    setNewNumber("");
  };

  const handleInput = event => {
    setNewName(event.target.value);
  };

  const handleNumberInput = event => {
    setNewNumber(event.target.value);
  };
  return (
    <form>
      <div className="form-name">
        <label>Name</label>
        <br />
        <input value={newName} onChange={handleInput} />
      </div>
      <div className="form-number">
        <label>Number</label>
        <br />
        <input value={newNumber} onChange={handleNumberInput} />
      </div>
      <div className="form-submit">
        <button onClick={handleSubmit}>
          <FontAwesomeIcon icon={faUserPlus} />
        </button>
      </div>
    </form>
  );
};

export default Form;
